.work-container .col div.content {
  padding: 5px;
  margin: 5px;
  font-size: 0.8em;
}

.work-container .col div.content ul {
  margin-bottom: 6px;
}

.work-container .center {
  padding: 5px;
}

@media only screen and (max-width: 600px) {
  .work-container .moon {
    left: 700px;
  }

  .work-container .stars {
    left: 300px;
  }

  .work-container .stars2 {
    left: 300px;
  }

  .work-container .stars3 {
    left: 300px;
  }
}

@media only screen and (max-width: 599px) {
  .work-container .moon {
    left: 100px;
    top: 0px;
  }

  .work-container .stars {
    left: 0px;
    top: 0px;
  }

  .work-container .stars2 {
    left: 0px;
    top: 0px;
  }

  .work-container .stars3 {
    left: 0px;
    top: 0px;
  }
}
